@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  color:white;
  font-family: 'Roboto', sans-serif;
}

.App {
  background-color: black;
  position:relative;
  text-align: center;
  height: 20vmin;
  pointer-events: none;
  margin:20% 2%;
}
.background_img{
  position: absolute;
  top: 0;
  height: 20vmin;
  width: 100%;
  z-index:0.999;
}
.weather_widget{
  display:grid;
  grid-template-columns:repeat(10, 1fr);
  grid-gap:0.25em;
  margin:0;
  padding:0;
}

.primary{
  display:grid;
  grid-template-columns:2fr 3fr 3fr;
  grid-gap:0.1em;
  grid-column:span 4;
  height:20vmin;
  z-index:1000;
}
.secondary{
  height:20vmin;
}

.prmy-icon{
  height:15vmin;
}

.prmy-description{
  padding:0;
  margin:0;
  line-height:2vmin;
  font-size:2vmin;
}

#prmy_section_middle{
  display:grid;
  grid-template-columns: 1fr 1fr;
}
#location{
  padding-top: 3vmin;
  grid-column:span 2;
  font-size:4vmin;
  line-height:6vmin;
}
#country_code{
  font-size:3vmin;
}
#main_temp{
  font-size: 5vmin;
}

#prmy_section_end{
  padding-left: 3vmin;
  text-align: left;
}
#date{
  font-size:2vmin;
  padding:2vmin 0 7vmin 0;
}
.prmy_section_end_section{
  font-size:2vmin;
  line-height:2vmin; 
}


.opacity_one{
  position:relative;
}
.opacity_two{
  position:absolute;
  height:100%;
  width:100%;
  background-color:black;
}
.secondary{
  height:20vmin;
}
.secondary_day{
  padding-top:2vmin;
  line-height: 3vmin;
}
.secondary_logo{
  height: 10vmin;
}
.secondary_temp{
  line-height: 3vmin;
  padding-bottom: 2vmin;
}

#one{
  opacity:0.1;
}
#two{
  opacity:0.2;
}
#three{
  opacity:0.4;
}
#four{
  opacity:0.6;
}
#five{
  opacity:0.8
}
#six{
  opacity:1;
}
